	module.exports = {"data":{"markdownRemark":{"html":"<p>The easiest way to deploy a MicroFeedback backend is with ZEIT’s\n<a href=\"https://zeit.co/now\">now</a>, which provides a command-line interface for\ndeploying Node.js microservices quickly and easily.</p>\n<p>Before you can start deploying, you’ll need the now client installed.\nSee <a href=\"https://zeit.co/now#get-started\">here</a> for instructions on how to\nget started with now.</p>\n<p>To deploy a MicroFeedback backend using the now CLI, pass the\nGitHub repo for backend.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-none\"><code>now microfeedback/microfeedback-github</code></pre>\n      </div>\n<p>You will be prompted to enter the required environment variables to\nconfigure your deployment. In the case of microfeedback-github, all you\nneed is a GitHub API token.</p>\n<p>See the <a href=\"/backends/\">Backends</a> page for a list of officially-supported backends.</p>","frontmatter":{"title":"Deployment"},"fields":{"path":"getting-started/deployment.md","slug":"/getting-started/deployment/","dir":"getting-started"}}},"pathContext":{"slug":"/getting-started/deployment/"}}

